@font-face{
  font-family: 'Blanka';
  font-weight: 300;
  src: local('Blanka-Regular'), url(./assets/fonts/Blanka-Regular.otf), format('opentype');
}

@font-face{
  font-family: 'Pixopedia';
  font-weight: normal;
  src: local('Pixopedia'), url(./assets/fonts/Pixopedia.ttf), format('truetype');
}

@font-face{
  font-family: 'Montserrat';
  src: local('Montserrat'), url(./assets/fonts/Montserrat-Black.ttf), format('truetype')
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  display: block;
  font-size: 16px;
  font-family: 'Montserrat';
}
