.ErrorModal{
    position: absolute;
    z-index: 300;
    top: -150px;
    left: 15px;
    width: 300px;
    
    height: 150px;
    background-color: rgb(173, 28, 28);
    box-shadow: 0 0 17px rgb(139, 16, 16);
    border-radius: 15px;
    padding: 10px;
    font-family: 'Montserrat';
    color: #273444;
    
}

.ErrorModal h3{
    font-size: 1.5rem;
}

.ErrorModal p{
    font-size: 1.3rem;
}

@keyframes fadeIn{
    from{
        transform: translateY(0);
    }
    to{
        transform: translateY(110%);
    }
}
@keyframes fadeOut{
    from{
        transform: translateY(110%);
    }
    to{
        transform: translateY(0);
    }
}



